<script>
  import { createEventDispatcher } from "svelte";
  import Icon from "../Icon";
  import createRipple from "../Ripple/ripple.js";

  export let icon = "";
  export let id = "";
  export let value = "";
  export let text = "";
  export let subheading = "";
  export let disabled = false;
  export let dense = false;
  export let navigation = false;
  export let to = "";
  export let selected = false;
  export let tabindex = null;
  export let basicClasses = "relative overflow-hidden my-1 transition p-4 cursor-pointer text-gray-700 flex items-center z-10";
  export let itemClasses = "";
  export let selectedClasses = "bg-gray-200";

  const ripple = createRipple();
  const dispatch = createEventDispatcher();

  function change() {
    if (disabled) return;
    value = id;
    dispatch('change', id);
  }
</script>

<style>li:focus {
  background-color: #fafafa;
}

.navigation {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  border-radius: 0.25rem;
  font-size: 0.875rem;
}

.navigation.selected {
  background-color: #f6e5f6;
  color: #b027b0;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<li
  use:ripple
  class="{basicClasses} {selected ? selectedClasses : ''}"
  class:navigation
  class:hover:bg-gray-transLight={!navigation}
  class:py-2={dense}
  class:text-gray-600={disabled}
  {tabindex}
  on:keypress={change}
  on:click={change}
  on:click>
  {#if icon}
    <Icon
      class="pr-6"
      small={dense}
      color={selected && navigation ? 'text-primary-500' : ''}>
      {icon}
    </Icon>
  {/if}

  <div class="flex flex-col p-0">
    <div class={itemClasses}>
      <slot>{text}</slot>
    </div>
    {#if subheading}
      <div class="text-gray-600 p-0 text-sm">{subheading}</div>
    {/if}
  </div>
</li>
