<script>
  import { afterUpdate, createEventDispatcher } from 'svelte';
  import DataCol from './DataCol.svelte';
  import { Icon } from "smelte";

  const dispatch = createEventDispatcher();

  export let edit = true;
  export let columns = [];
  export let rows = [];
  export let selected = null;

  function setRowCol(settings, i, j) {
    settings.dataset = { row: i, col: j };
    return settings;
  }

  let prevSelected;
  afterUpdate(() => {
    if (prevSelected && selected) {
      let s = prevSelected;
      const el = table.querySelector( 
          `[data-row="${s[0]}"][data-col="${s[1]}"]` 
      );
      // console.log(s, el);
      if (el) el.focus();
    }
  });

  function actionClick(event, row, action) {
      event && event.preventDefault();
      action && action(row);
  }

  function arrow(e) {
    const { target: node, keyCode: code} = e;
    if (code < 37 || code > 40) return;

    let i = +node.dataset.row;
    let j = +node.dataset.col;

    if (code === 37) j = Math.max(0, j - 1);
    if (code === 39) j = Math.min(j + 1, columns.length - 1);
    if (code === 38) i = Math.max(0, i - 1);
    if (code === 40) i = Math.min(i + 1, rows.length - 1);
    
    selected = [ i, j ];
  }

  export let asc = false;
  let sortBy = null;

  $: sorted = sort(sortBy);

  function sort(col) {
    if (!col) return rows;

    if (col.sort) return col.sort(rows);

    const sorted = rows.sort((a, b) => {
      const valA = col.value ? col.value(a) : a[col.field];
      const valB = col.value ?  col.value(b) : b[col.field];

      const first = asc ? valA : valB;
      const second = asc ? valB : valA;

      if (typeof valA === 'number') {
        return first - second;
      }

      return ('' + first).localeCompare(second);
    });
  console.log(sorted);
    return sorted;
  }    
</script>

<style>table {
  border-radius: 0.25rem;
  box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.20), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
  padding: 0.5rem;
  font-size: 0.875rem
}

table th, table td {
  padding: 0.75rem;
  font-weight: 400;
  text-align: left;
}

table th:first-child, table td:first-child {
  text-align: left;
  border-right-width: 1px;
}

table th {
  color: #757575;
  font-size: 0.75rem;
  cursor: pointer
}

table th .asc {
  transform: rotate(180deg);
}

table th .sort-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

table th:first-child .sort-wrapper {
  justify-content: flex-start;
}

table th .sort {
  width: 1rem;
  height: 1rem;
  opacity: 0;
  transition: .1s;
}

table th:hover {
  color: #000;
  transition: .1s
}

table th:hover .sort {
  opacity: 1;
}

table tr {
  border-color: #eeeeee;
  border-top-width: 1px;
  border-bottom-width: 1px;
  padding-left: 0.75rem;
  padding-right: 0.75rem
}

table tr:hover {
  background-color: #fafafa;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<table class="p-1">
  <thead class="items-center">
    <tr>
    {#each columns as column, i}
      <slot name="header">
        <th
          class="capitalize"
          on:click={() => {
            if (column.sortable === false) return;
            asc = sortBy === column ? !asc : false;
            sortBy = column;
          }}
        >
          <div class="sort-wrapper">
            {#if column.sortable !== false}
              <span class="sort" class:asc={!asc && sortBy === column}>
                <Icon small color="text-gray-400">arrow_downward</Icon>
              </span>
            {/if}
            <span>{column.label || column.field}</span>
          </div>
        </th>
      </slot>
    {/each}
    </tr>
  </thead>

  <tbody>
    {#each rows as row, i}
      <tr>
        {#each columns as column, j}
          <td class="{column.class || ''} { ((!edit && column.action) || edit) ? 'nopadding' : '' } { column.numeric ? 'numeric' : ''} { column.truncate ? ' truncate' : '' }" 
              style="width: { column.width ? column.width : 'auto' }">
            <DataCol bind:source="{row}" {edit} 
              settings="{setRowCol(column, i, j)}"
              on:change="{event => dispatch('update', { event })}" 
              on:click="{event => actionClick(event, row, column.action)}"
              on:keydown="{arrow}"
              on:focus="{() => selected = [i, j]}"
            />
          </td>    
        {/each}
      </tr>
    {/each}
  </tbody>
</table>

